'use client';

import styles from './header.module.scss';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

const headerItems = {
  home: { title: 'главная', href: '/' },
  about: { title: 'о нас', href: '/about' },
  contacts: { title: 'контакты', href: '/contacts' },
  vacancies: { title: 'вакансии', href: '/vacancies' }
};

export default function Header() {
  const pathname = usePathname();

  const isActive = (href: string) => {
    return pathname === href
  };

  return (
    <header className={styles.header}>
      {
        Object.entries(headerItems).map(([key, value]) => 
          <Link className={`${styles.item} ${isActive(value.href) && styles.active}`} href={value.href} key={key}>
            {value.title.toUpperCase()}
          </Link>
        )
      }
    </header>
  );
}
